	module.exports = {"pathContext":{"group":[{"node":{"fields":{"title":"Mama on the Beach","date":"12 Sep 2004","slug":"/2004-09-12-mama-on-the-beach/","tags":"Y2004"},"html":"<p><a href=\"http://www.flickr.com/photo.gne?id=416104\"><img src=\"http://www.flickr.com/photos/416104_m.jpg\" border=\"0\" alt=\"\" /></a></p>\n<p>I've posted some <a href=\"http://www.flickr.com/photos/papascott/sets/9888/\">Florida pictures</a> <a href=\"http://www.flickr.com/photos/papascott/sets/9888/show\">(as slideshow)</a> at <a href=\"http://www.flickr.com/photos/papascott/\">Flickr</a>. I'm still figuring out how it works.</p>"},"next":{"fields":{"title":"A Guy In His Pajamas","date":"12 Sep 2004","slug":"/2004-09-12-a-guy-in-his-pajamas/"}},"previous":{"fields":{"title":"Leuchtturm","date":"13 Sep 2004","slug":"/2004-09-13-leuchtturm/"}}},{"node":{"fields":{"title":"A Guy In His Pajamas","date":"12 Sep 2004","slug":"/2004-09-12-a-guy-in-his-pajamas/","tags":"Y2004"},"html":"<p>If you post cat pictures to a blog, you got to make sure that you've <a href=\"http://instapundit.com/archives/017736.php\">dressed the part</a>: \"Bloggers have no checks and balances . . . [it's] a guy sitting in his living room in his pajamas.\"</p>"},"next":{"fields":{"title":"Remember the Day","date":"11 Sep 2004","slug":"/2004-09-11-remember-the-day/"}},"previous":{"fields":{"title":"Mama on the Beach","date":"12 Sep 2004","slug":"/2004-09-12-mama-on-the-beach/"}}},{"node":{"fields":{"title":"Remember the Day","date":"11 Sep 2004","slug":"/2004-09-11-remember-the-day/","tags":"Y2004"},"html":"<p>Can you remember the day of the week of the important dates in your life?</p>\n<p>I was married on Tuesday. That's easy, it was the day after Labor Day. My son was born early on Monday morning, the night of a killer storm. We arrived in Germany to stay on Saturday afternoon; all the shops were closed.</p>\n<p>The Twins won the '87 World Series on Sunday, but the 7th game of the World Series always falls on Sunday. The same for the last German World Cup victory. World Cup finals always fall on Sunday, both for men and for women.</p>\n<p>Sometimes my memory plays tricks on me. I remember the Challenger exploding on Wednesday, and the Berlin Wall falling on Friday, but both are a day too late.</p>\n<p><a href=\"http://www.papascott.de/archives/2001/09/12/the-day-the-earth-stood-still/\">9/11 was on Tuesday. I learned of the first crash at 3pm Hamburg time. The towers fell 90 minutes later as I was driving home from work, listening to NDRInfo. At home our satellite receiver was broken; we tried desparately to set up a tabletop antenna to watch ARD.</a></p>\n<p>Some days I remember better than others.</p>"},"next":{"fields":{"title":"Enough For Now","date":"11 Sep 2004","slug":"/2004-09-11-enough-for-now/"}},"previous":{"fields":{"title":"A Guy In His Pajamas","date":"12 Sep 2004","slug":"/2004-09-12-a-guy-in-his-pajamas/"}}}],"pathPrefix":"","first":false,"last":false,"index":423,"pageCount":1101}}

